@use 'base/mixins' as m;
@use 'base/variables' as v;

.contact {

    &__container {
        row-gap: 3rem;
    }

    &__information {
        display: flex;
        margin-bottom: v.$mb-2;
    }

    &__icon {
        font-size: 2rem;
        color: var(--first-color);
        margin-right: v.$mb-0-75;
    }

    &__title {
        font-size: v.$h3-font-size;
        font-weight: v.$font-medium;
    }

    &__subtitle {
        font-size: v.$small-font-size;
        color: var(--text-color-light);
    }

    &__content {
        background-color: var(--input-color);
        border-radius: .5rem;
        padding: .75rem 1rem .25rem;
    }

    &__label {
        font-size: v.$smaller-font-size;
        color: var(--title-color);
        font-weight: 500;
    }

    &__input {
        width: 100%;
        background-color: var(--input-color);
        color: var(--text-color);
        font-family: v.$body-font;
        font-size: v.$normal-font-size;
        border: none;
        outline: none;
        padding: .25rem .5rem .5rem 0;

        &::placeholder {
            font-size: v.$smaller-font-size;
            color: var(--text-color-light);
        }

        
    }

    &__tel,
    &__mail {
        color: var(--text-color-light);
    }

    &__legend {
        font-style: italic;
        font-size: v.$small-font-size;
    }

    &__alert {
        background-color: #fa6b6b;
        color: #FFF;
        padding: .5rem;
        text-align: center;
        font-size: v.$small-font-size;
        border-radius: .2rem;
    }
}

.opacity-50 {
    opacity: 0.5;
}

.succed {
    background-color: rgb(132, 243, 132);
    color: #FFF;
    padding: .5rem;
    text-align: center;
    margin: .1rem;
    font-weight: 700;
    text-transform: uppercase;
    font-size: v.$small-font-size;
    border-radius: .2rem;
}



